.tasks{
  text-align: right;
    padding: 20px;
    &__btn-holder{
        text-align: right;
        display: block;
    }

    &__add-btn{
        padding: 10px 15px;
        font-size: 14px;
        font-weight: 600;
        color: #fff;
        background-color:  #fb8c00;
        border: none;
        border-radius: 4px;
        cursor: pointer;
        transition: background-color 0.3s;
        margin-bottom: 20px;

        @media (max-width: 768px) {
          margin-bottom: 15px;
        }

        @media (max-width: 480px) {
          margin-bottom: 10px;
        }
    
        &:hover {
          background-color: #df7c02; 
        }
    }

    &__none{
        text-align: center;
        padding: 30px 0;
    }

    &__filter{
      padding: 5px 10px;
      background-color:#fb8c00;
      border-radius: 7px;
      color:#fff;

    }

    table {
        width: 100%;
        border-collapse: collapse;
        margin-top: 15px;
        overflow: hidden;
    
        th, td {
          padding: 12px 15px;
          text-align: left;
          border-bottom: 1px solid #eaeaea;
          @media (max-width: 768px) {
            padding: 8px 10px;
            font-size: 14px;
          }
        }
    
        th {
          font-weight: 600;
          color:#fb8c00;
          text-transform: uppercase;
          font-size: 14px;
          @media (max-width: 768px) {
            font-size: 12px;
          }
        }
}
}

@media (max-width: 768px) {

    tr {
    display: flex;
      margin-bottom: 15px;
      justify-content: space-between;
      border-bottom: 1px solid #eaeaea;
      overflow: hidden;
    }

    td {
      display: flex;
      border:none;
      justify-content: space-between;
      padding: 10px;
      font-size: 14px;
    }
  }


  @media (max-width: 520px) {

    tr {
      margin-bottom: 10px;
    }

    td {
      padding: 5px;
      font-size: 12px;
    }
  }

  @media (max-width: 430px) {
    table {
      display: block;
    }
    tbody{
      display: flex;
      flex-direction: row;
      gap: 10px;
      flex-wrap: wrap;
    }
  
    tr {
      display: block;
    }
  
    th {
      display: none; 
    }
  
    td {
      display: block;
      text-align: left;
      border: none;
      padding: 5px 10px;
    }
  }

.modal-overlay {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-color: rgba(50, 50, 50, 0.929); 
    z-index: 999; 
  }
  

